import React from "react";
import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import {
  Col,
  Row
} from "antd";
import { compose } from "redux";
import { connect } from "react-redux";

class DataView extends React.Component {
  shouldComponentUpdate(nextProps) {
    return JSON.stringify(this.props.filterSelect) !== JSON.stringify(nextProps.filterSelect) || JSON.stringify(this.props.auth) !== JSON.stringify(nextProps.auth)
  }

  render() {
    // console.log("re-render")
    const { datas, loading } = this.props
    return <>{
      datas.map((data, index) => {
        data[1][0].transaction_type_string = this.props.renderTransTypeString(data[1][0].transaction_type)
        data.dataSource = []
        var totalCredit = []
        var totalDebit = []
        data[1].map(x => {
          if (x.debit_credit === 1) {
            totalDebit.push(x.amount)
            return data.dataSource.push({
              ...x,
              key: x.id_gl_transaction,
              debit: x.amount,
              credit: null,
            });
          } else {
            totalCredit.push(x.amount)
            return data.dataSource.push({
              ...x,
              key: x.id_gl_transaction,
              debit: null,
              credit: x.amount,
            });
          }
        })

        var debit = ""
        var credit = ""
        if (totalCredit.length > 0) {
          credit = totalCredit.reduce(function (a, b) { return parseFloat(a) + parseFloat(b) })
        }
        if (totalDebit.length > 0) {
          debit = totalDebit.reduce(function (a, b) { return parseFloat(a) + parseFloat(b) })
        }
        data.dataSource.push({
          key: data[1][0].id_gl_transaction + data[1][0].id_source_transaction,
          account_code: "Total",
          debit: debit,
          credit: credit
        })
        // console.log("data parent")
        return (
          <div key={index}>
            <Row style={{ fontSize: "16px", marginBottom: "5px" }}>
              <Col span={4}><strong><IntlMessages id="salesReturns.filter.date"></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={19}>{moment(data[1][0].transaction_date).format("DD MMMM YYYY HH:mm:ss")}</Col>
            </Row>
            <Row style={{ fontSize: "16px", marginBottom: "5px" }}>
              <Col span={4}><strong><IntlMessages id="transactionType"></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={19}>{data[1][0].transaction_type_string}</Col>
            </Row>
            <Row style={{ fontSize: "16px", marginBottom: "5px" }}>
              <Col span={4}><strong><IntlMessages id="transactionNo"></IntlMessages></strong></Col>
              <Col span={1}>:</Col>
              <Col span={19}>{data[1][0].source_transaction_no}</Col>
            </Row>
            <div style={{ padding: "0 20px 20px" }} >
              <TableWrapper
                bordered={true}
                dataSource={data.dataSource}
                columns={this.props.miniColumns}
                className="invoiceListTable"
                loading={loading}
                pagination={false}
              />
            </div>
          </div>
        )
      })
    }
    </>
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth
  }
}

export default compose(
  connect(mapStateToProps)
)(DataView);